<template>
    <el-row>
        <div id="echart" style="width:100%;height:350px"></div>
    </el-row>   
</template>
<script>
 var echarts=require('echarts')
 
export default {
    data(){
        return {
              lineChartData:{
                newVisitis: {
                    adv: [100, 120, 161, 134, 105, 160, 165],
                    video: [120, 82, 91, 154, 162, 140, 145],
                    seo:[120, 182, 291, 154, 162, 140, 145]
                },
                messages: {
                    adv: [200, 192, 120, 144, 160, 130, 140],
                    video: [180, 160, 151, 106, 145, 150, 130],
                    seo:[120, 182, 291, 154, 162, 140, 145]
                },
                purchases: {
                    adv: [80, 100, 121, 104, 105, 90, 100],
                    video: [120, 90, 100, 138, 142, 130, 130],
                    seo:[120, 182, 291, 154, 162, 140, 145]
                },
                shoppings: {
                    adv: [130, 140, 141, 142, 145, 150, 160],
                    video: [120, 82, 91, 154, 162, 140, 130],
                    seo:[120, 182, 21, 154, 162, 140, 145]
                }
             }
        }
    },
    mounted(){
        this.initCharts()
    },
    props:{
        message:{
            type:String,
            required:true
        }
    },
    watch:{
        message:{
            handler: function (val, oldVal) {  
                 this.setOptions(this.lineChartData[val]);
             },
            deep: true
        }
    },
    methods:{
        initCharts(){ 
             this.myChart=echarts.init(document.getElementById('echart'))
            //  console.log(lineChartData[this.message])
             this.setOptions(this.lineChartData[this.message]);
        },
        setOptions({adv,video,seo}={}){  //,adv ,video,seo
            this.myChart.setOption({
                title: {
                    text: ''
                },
                tooltip: {
                    trigger:'axis'
                },
                legend: {
                      data: [ '', '', '', '']
                 },
                 grid:{
                    left:'3%',
                    right:'4%',
                    bottom:'3%',
                    containLabel:true
                 },
                 toolbox:{
                    feature:{
                        saveAsImage:{}
                    }
                 },
                xAxis: {
                    type:'category',
                    boundaryGap:false,
                    data: [ '','','','','','','']
                },
                yAxis: {
                    type:'value'
                },
                series: [
                   
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        data:adv
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        data:video
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        data:seo
                    }
                   
                ]      
            })   
        }
    }
}   
</script>
<style>

</style>



